import { CangjieInputEvent, Controller } from '@ali/4ever-cangjie';
import { HoverCapture } from '../utils/hoverCapture';
import { IHoverToolbarPluginConfigs } from '../types';

export default function createOnCanjigeInput(config: IHoverToolbarPluginConfigs, hoverCapture: HoverCapture) {
  const isDisabled = config.isDisabled && config?.isDisabled();
  return function (event: CangjieInputEvent, controller: Controller, next) {
    if (!isDisabled) {
      hoverCapture.getCurrentNode() && hoverCapture.forceClose();
    }
    return next();
  };
}
